@media (max-width: 1200px) {
    .container__small,
    .container__medium,
    .container__slide {
      min-width: 100%;
      padding: 0 1em !important;
      margin: 0em auto !important;
    }
    article {
      box-shadow: none !important;
    }
  }
  
@media (max-width: 850px) {
    .container__small,
    .container__medium,
    .container__slide {
      padding: 0 0.5em;
    }
  }