/*====================
    * NAVBAR *
====================*/
 .navbar {
	 background: var(--bg-navbar);
	 color: white;
	 padding: 0.5rem 1em;
}
 .navbar__container {
	 padding: 0.5em;
	 margin: 0em;
	 display: grid;
	 grid-template-columns: 1fr auto 0fr 0fr 0fr;
	 grid-template-rows: auto auto;
	 align-items: center;
}
 .navbar__logo {
	 justify-self: start;
}
 .navbar__logo a {
	 font-family: var(--font-logo);
	 font-size: 2.5rem;
	 font-weight: bold;
	 color: var(--color1);
	 cursor: pointer;
	 grid-column: 1;
}
 .navbar__icon i {
	 color: white;
	 cursor: pointer;
	 display: block;
	 font-size: 2em;
	 color: var(--color1);
}
 .navbar__items {
	 margin: 1em 0em;
	 grid-column: 1;
	 grid-row: 2;
	 display: none;
}
 .navbar__items ul {
	 display: flex;
	 flex-direction: column;
	 align-items: stretch;
}
 .navbar__items ul li {
	 text-decoration: none;
	 list-style: none;
}
 .navbar__links {
	 color: white;
	 display: block;
	 padding: 0.2em 0.5em;
	 margin: 0.5em 0.5em;
}
 .navbar__dark {
	 display: block;
}
 .navbar__color {
	 display: block;
	 
}
 @media (min-width: 1024px) {
	 .navbar__container {
		 grid-template-rows: auto 0em;
		 margin: 0em 2em;
	}
	 .navbar__icon i {
		 visibility: hidden;
	}
	 .navbar__items {
		 padding: 0em;
		 margin: 0em;
		 align-items: center;
		 grid-column: 2;
		 grid-row: 1;
		 display:block;
	}
	 .navbar__items ul {
		 align-items: center;
		 flex-grow: initial;
		 justify-items: flex-end;
		 flex-direction: row;
	}
	 .navbar__links {
		 padding: 0.2em;
		 margin: 0em 1em;
	}
	 .navbar__btn {
		 border-radius: 0.1em;
		 padding: 0.4em;
		 background: var(--color1);
	}
}