/* ====================
    * CARDS *
==================== */
:root {
  --heigh-post: 23rem;
  --width-post: 20rem;
}

.panel-title {
  margin: 0em;
  margin-top: -1em;
  background-color: var(--color1);
  color: var(--white);
}

.panel-title h1 {
  font-size: 4rem;
  color: var(--white);
}

.panel-title h3 {
  color: var(--white);
}

.card {
  box-shadow: var(--box-shadow);
  background: var(--white);
  padding: 2em;
  margin-bottom: 1em;
}

.card.img {
  box-shadow: var(--box-shadow);
  padding: 0em;
  width: 90%;
  height: auto;
  margin: 1em auto;
}
.card.item {
  margin: 1em;
  padding: 1em;
  border-radius: 2em;
  box-shadow: var(--box-shadow);
  background-color: var(--color2);
}
.card.subitem {
  box-shadow: none;
  border-radius: 2em;
  border: 0.2em solid var(--black);
  background-color: var(--color2);
}
.card.note {
  margin: 1em 0.5em;
  padding: 0.7em;
  border-left: solid 0.6em var(--color1);
  border-radius: 0em;
  background-color: var(--color2);
  background: var(--white);
}
/* ====================
    * CARD POST *
==================== */
.card-post {
  margin: 1em;
  padding: 0em;
  background-color: var(--white);
  border-radius: 2em;
  box-shadow: var(--box-shadow);
  overflow: hidden;
  min-width: var(--width-post);
  max-width: var(--width-post);
  min-height: var(--heigh-post);
  max-height: var(--heigh-post);
  line-height: 1rem;
}
.card-post__top {
  margin: 0em;
  padding: 0em;
}
.card-post__img {
  margin: 0em;
  padding: 0em;
  width: 100%;
  height: 10rem;
  top: 0;
  object-fit: cover;
}
.card-post__info {
  position: relative;
  padding: 0.75rem 1.25rem;
  transition: all 200ms ease-in-out;
}
.card-post__about {
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: all 200ms ease-in-out;
}
.card-post__tag {
  padding: 0.2rem 0.2rem;
  font-size: 1em;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 1px;
  background: var(--color1);
  color: var(--white);
  cursor: pointer;
}
.card-post__title {
  z-index: 10;
  transition: all 350ms ease-in-out;
  color: var(--color1);
}
.card-post__creator {
  padding-bottom: 0.75rem;
  transition: all 250ms ease-in-out;
  text-align: right;
}

/**/
/* * SLIDE * */
.container__slide {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  padding: 1em;
}
.container__slide section {
  width: 100%;
  /* height: 300px; */
  margin: 1em auto;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: nowrap;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  /* border: 1px solid black; */
}
.container__slide section::-webkit-scrollbar {
  width: 1em;
}

.container__slide section::-webkit-scrollbar-track {
  background: var(--color2);
  border-radius: 1em;
}

.container__slide section::-webkit-scrollbar-thumb {
  background: var(--color1);
  border-radius: 1em;
}

.container__slide section::-webkit-scrollbar-thumb:hover {
  background: var(--color1);
}

