.subscribe {
    position: relative;
    width: 400px;
    height: 270px;
    background-size: 100%;
    background-repeat: no-repeat;
    background-position-y: -600%;
    background-color: #000;
    border-radius: 4px;
    color: #FFF;
    box-shadow: 0px 0px 50px 5px rgba(0, 0, 0, 0.5);
    margin: 10rem auto;
    
  }
  
  
  .subscribe__wrapper {
    position: absolute;
    left: -30px;
    right: -30px;
    height: 200px;
    padding: 30px;
    background-position-x: 272%;
    background-position-y: -1px;
    background-color: #FFF;
    border-radius: 4px;
    color: #333;
    box-shadow: 0px 0px 60px 5px rgba(0, 0, 0, 0.4);
  }
  
  .subscribe__wrapper:after {
    position: absolute;
    content: "";
    right: -10px;
    bottom: 71px;
    width: 0;
    height: 0;
    border-bottom: 10px solid var(--color1);
    
  }
  
  .subscribe__wrapper h4 {
    text-align: center;
    letter-spacing: 3px;
    line-height: 28px;
  }
  
  .subscribe__wrapper input {
    position: absolute;
    bottom: 30px;
    border: none;
    border-bottom: 1px solid #d4d4d4;
    padding: 10px;
    width: 65%;
    background: transparent;
    transition: all .25s ease;
    font-size: var(--font-size);
  }
  

  
  .subscribe__wrapper .submit-btn {
    position: absolute;
    border-radius: 30px;
    border-bottom-right-radius: 0;
    border-top-right-radius: 0;
    background-color: var(--color1);
    color: #FFF;
    padding: 12px 25px;
    display: inline-block;
    font-weight: bold;
    letter-spacing: 2px;
    right: -10px;
    bottom: 30px;
    cursor: pointer;
    transition: all .25s ease;
    box-shadow: -5px 6px 20px 0px rgba(51, 51, 51, 0.4);
  }
  
  .subscribe__wrapper .submit-btn:hover {
    background-color: var(--color2);
    color: var(--color1);
  }